.icon { display: inline-block; fill: currentColor; font-size: inherit; }

.lazy.is-loaded:not(.no-animation) { animation: fadeIn 1s; }

.out { position: relative; z-index: 1; }

.canvas-bg { position: fixed; width: 100%; height: 100%; left: 0; top: 0; z-index: 0; }

.canvas-bg canvas { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.canvas-grid { position: relative; padding-top: 30%; max-width: 500px; }

.canvas-grid canvas { position: absolute; top: 0; left: 0; bottom: 0; right: 0; }

.content { transform-origin: top center; }

.nav { padding-top: 20px; padding-bottom: 20px; text-align: center; }

.nav li { display: inline-block; margin-right: 10px; margin-left: 10px; text-transform: uppercase; }

.no-touch .nav a:hover { text-decoration: underline; }

.header { position: fixed; top: 0; left: 0; right: 0; z-index: 10; height: 60px; }

.header.is-white { color: #fff; }

.overlay { position: fixed; top: 0; bottom: 0; left: 0; right: 0; z-index: 10; visibility: hidden; opacity: 0; background-color: #000; }

.slider__wrap { position: relative; }

.slider__wrap * { box-sizing: border-box; }

.slide { height: 200px; display: flex; justify-content: center; align-items: center; flex-direction: row; flex-wrap: nowrap; background-color: #cccccc; }

.products { display: grid; grid-gap: 10px; grid-template-columns: 1fr 1fr; }

.product { display: block; padding-top: 50%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; position: relative; }

.product::before { content: ''; position: absolute; top: 0; left: 0; bottom: 0; right: 0; z-index: 0; background-color: rgba(0, 0, 0, 0.4); }

.product__title { color: #fff; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 100%; text-transform: uppercase; text-align: center; z-index: 1; }

.hero { height: 100vh; padding-top: 60px; position: relative; display: flex; justify-content: center; align-items: center; flex-direction: row; flex-wrap: nowrap; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.hero::before { content: ''; position: absolute; top: 0; left: 0; bottom: 0; right: 0; z-index: 0; background-color: rgba(0, 0, 0, 0.4); }

.hero h1 { color: #fff; text-transform: uppercase; font-size: 6vw; position: relative; z-index: 1; }

.hero--simple { display: block; height: auto; }

.hero--simple h1 { color: inherit; }

.hero--simple::before { content: ''; display: none; }

.section { padding: 10%; padding-top: 50%; padding-bottom: 50%; color: #fff; background-color: orange; }

.section p { max-width: 300px; text-align: justify; }

.cover-image { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; position: fixed; z-index: 0; top: 0; bottom: 0; right: 0; left: 0; z-index: 20; transform-origin: 0 0; }

.cover-image::before { content: ''; position: absolute; top: 0; left: 0; bottom: 0; right: 0; z-index: 0; background-color: rgba(0, 0, 0, 0.4); }

.cover-image.is-animate { transition: transform 1s; }

.btn { display: inline-block; background-color: #000; color: #fff; padding: .5em; text-align: center; min-width: 200px; text-transform: uppercase; }

.popup { position: fixed; top: 0; bottom: 0; left: 0; right: 0; z-index: 20; background-color: rgba(0, 0, 0, 0.7); padding: 15px; overflow: auto; text-align: center; font-size: 0; visibility: hidden; opacity: 0; }

.popup:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.popup.is-active { visibility: visible; opacity: 1; }

.popup__inner { display: inline-block; vertical-align: middle; background-color: #fff; width: 100%; max-width: 450px; font-size: 16px; padding: 50px; position: relative; }

.popup__close { position: relative; display: inline-block; width: 20px; height: 20px; position: absolute; top: 15px; right: 15px; z-index: 1; }

.popup__close::before, .popup__close::after { position: absolute; content: ''; width: 1px; height: 100%; background-color: currentColor; top: 0; left: 50%; }

.popup__close::before { transform: rotate(45deg); }

.popup__close::after { transform: rotate(-45deg); }

.parallax-img { overflow: hidden; font-size: 0; display: inline-block; width: 50%; padding-top: 40%; position: relative; margin-bottom: 500px; }

.parallax-img__img { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; position: absolute; top: -50px; bottom: -50px; right: -50px; left: -50px; }

.scrollbar { max-height: 500px; overflow: auto; position: relative; }

.scrollbar::-webkit-scrollbar { width: 6px; }

.scrollbar::-webkit-scrollbar-track { background-color: black; box-shadow: inset 2px 0 0px orange, inset -2px 0 0px orange; outline: none; }

.scrollbar::-webkit-scrollbar-thumb { background-color: lightblue; transition: background-color 0.3s; }

.no-touch .scrollbar::-webkit-scrollbar-thumb:hover { background-color: blue; }

.scrollbar p { margin-bottom: 15px; max-width: none; }

.simplebar-track.simplebar-vertical { position: absolute; right: 0; top: 0; bottom: 0; width: 8px; background-color: #fff; z-index: 1; }

.simplebar-scrollbar { width: 100%; right: 0; background-color: #000; }

.simplebar-scrollbar::before { content: ''; display: none; }

input[type=number] { display: inline-block; appearance: textfield; }

input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button { appearance: none; }

input[type="number"]::-ms-clear { display: none; }

video { max-width: 100%; }

.fadeIn { animation-name: fadeIn; }

@keyframes fadeIn { 0% { opacity: 0; }
  100% { opacity: 1; } }

.header { display: flex; justify-content: space-between; align-items: center; flex-direction: row; flex-wrap: nowrap; }

.burger { display: block; width: 28px; height: 28px; position: relative; }

.burger span { display: block; }

.burger span, .burger span::after, .burger span::before { width: 100%; height: 2px; background-color: currentColor; backface-visibility: hidden; border-radius: 2px; transition: transform 0.3s; }

.burger span::after, .burger span::before { content: ""; position: absolute; left: 0; }

.burger span::after { top: 5px; }

.burger span::before { bottom: 5px; }

.burger--active span { background-color: transparent; }

.burger--active span::before { transform: rotate(45deg) translate(-7px, -5px); }

.burger--active span::after { transform: rotate(-45deg) translate(-6px, 4px); }

.overflow-hidden { overflow: hidden; }
